import React from 'react'
import { NavLink } from 'react-router-dom'

import sprite from '../assets/images/sprite.svg'

// import devLogo from "../assets/images/logo/develop-logo.png"

function Footer(props) {
    
    const list = props.linksList

    return (
        <footer className="footer footer_theme_2">
            <div className="container">
                <div className="row justify-content-between">
                    <div className="col-lg-6">
                        <div className="row "> 
                        {/* justify-content-center */}
                            <div className="col-sm-6">
                                <nav className="main-menu main-menu_theme_4 footer__main-menu">
                                <ul className="main-menu__list">
                                {
                                    list.length > 0 &&
                                    list.map( (item, index) => {
                                        return (
                                            <li key={index} className="main-menu__item"> 
                                                <NavLink className="main-menu__link" to={`/${item.slug}`}>
                                                    {item.title} 
                                                </NavLink> 
                                            </li>
                                        )
                                    })
                                }
                                </ul>
                                </nav>
                            </div>
                            {/* <div className="col-sm-6">
                                <nav className="main-menu main-menu_theme_4 footer__main-menu">
                                <ul className="main-menu__list">
                                    <li className="main-menu__item"><a className="main-menu__link" href="##">Новости</a></li>
                                    <li className="main-menu__item"><a className="main-menu__link" href="##">Контакты</a></li>
                                </ul>
                                </nav>
                            </div> */}
                        </div>
                    </div>
                    <div className="col-lg-6">
                        <div className="row align-items-center justify-content-center justify-content-sm-start">
                        <div className="col-xl-6 col-lg-5 col-sm-6"><a className="phone footer__phone" href="tel:+78007008000">
                            {/* Если указана иконка*/}
                            {/*Если потребуются свои элементы, то можно указать поcле +phone()*/}<span className="phone__number">8 800 700 8000</span></a><a className="phone footer__phone" href="tel:+78007008000">
                            {/* Если указана иконка*/}
                            {/*Если потребуются свои элементы, то можно указать поcле +phone()*/}<span className="phone__number">8 800 700 8000</span></a></div>
                        <div className="col-xl-6 col-lg-7 col-sm-6 footer__adress-wrapper">
                            <div className="footer__adress"><svg className="icon icon_placeholder footer__adress-icon" role="img">
                                <use href={sprite + "#placeholder"}  />
                            </svg><span className="footer__adress-name">Здесь адрес</span></div>
                            <div className="footer__adress"><svg className="icon icon_clock footer__adress-icon" role="img">
                                <use href={sprite + "#clock"}  /> 
                            </svg><span className="footer__adress-name">Пн-Пт с 10.00 до 18.00, Сб-Вс выходной</span></div>
                        </div>
                        </div>
                    </div>
                </div>
                <div className="row justify-content-between align-items-center">
                    <div className="col-lg-3 col-md-7">
                        <div className="footer__logo-title">Ремонт компьютеров в СПб</div>
                    </div>
                    {/* <div className="col-xl-6 col-lg-5 col-md-7">
                        <div className="footer__tags"> Компания -
                            <NavLink className="footer__tag" to="#">тег-1</NavLink>,
                            <NavLink className="footer__tag" to="#">тег-2</NavLink>,
                            <NavLink className="footer__tag" to="#">тег-3</NavLink>,
                            <NavLink className="footer__tag" to="#">тег-4</NavLink>,
                            <NavLink className="footer__tag" to="#">тег-5</NavLink></div>
                        <div className="footer__copyright">© 2010 - 2020</div>
                    </div>
                    <div className="col-xl-3 col-lg-4 col-md-5">
                        <div className="footer__develop"><span className="footer__develop-name">Разработано в - </span><a className="footer__develop-link" href="https://len-studio.ru/"><img className="footer__develop-img" src={devLogo} alt="len-studio.ru" /></a></div>
                    </div> */}
                </div>
            </div>
        </footer>

        

    )
}

export default Footer
