import React from 'react'
import { NavLink } from 'react-router-dom';  

import logo from '../assets/images/logo/computer.svg'
import sprite from '../assets/images/sprite.svg'

function Header(props) { 

    const list = props.linksList

    const [isOpen, setIsOpen] = React.useState(false);

    const handleClick = () => {
        setIsOpen(!isOpen)
    } 
    const logoLink = { 
      display: 'flex',
      alignItems: 'center' 
    }
    const logoStyle = { 
      width: 40,  
      height: 30,  
    }
    const logoTitle = { 
      display: 'flex',
      flexDirection: 'column',  
      fontSize: 16, 
      fontWeight: 700,
      color: '#000' 
    }
    const logoSubTitle = { 
      fontSize: 12, 
      fontWeight: 700,
      color: '#dedede', 
      marginTop: 4 
    }

    return ( 
        <header className="header header_theme_3">
          <div className="header__top">
            <div className="container header__container">
                <NavLink className="logo " style={logoLink} to="/">
                    <img src={logo} style={logoStyle} alt='logo' />  
                    <span style={logoTitle}>
                        Ремонт компьютеров
                      <span style={logoSubTitle}>В Санкт Петербурге</span>
                    </span>
                    
                </NavLink>
              <div className="header__left">
                <div className="header__phone">
                    <a className="phone phone_theme_3" href="tel:+78007008000">
                    {/* Если указана иконка*/}<svg className="icon icon_phone-call phone__icon" role="img">
                      <use href={sprite + "#phone-call"} />
                    </svg>
                    {/*Если потребуются свои элементы, то можно указать поcле +phone()*/}<span className="phone__number">8 800 700 8000</span></a>
                    <a className="phone phone_theme_3" href="tel:+78007008000">
                    {/* Если указана иконка*/}<svg className="icon icon_phone-call phone__icon" role="img">
                        <use href={sprite + "#phone-call"} />
                    </svg>
                    {/*Если потребуются свои элементы, то можно указать поcле +phone()*/}<span className="phone__number">8 800 700 8000</span></a></div>
                <div className="header__btn">
                  <button className="button button_theme_12" type="button" onClick={props.handleShowModal}>
                    <svg className="icon icon_phone-call button__icon" role="img">
                        <use href={sprite + "#phone-call"} />
                    </svg>
                    <span className="button__text">Заказать звонок</span>
                  </button></div>
                <div className="header__busket"><svg className="icon icon_shopping-cart" role="img">
                    <use href={sprite + "#shopping-cart"} />
                  </svg><span className="header__busket-name">корзина</span><span className="header__busket-count">1</span></div>
                <div className="header__mobile-menu-trigger">
                      <div
                          className="hamburger-btn js-btn-menu"
                          role="button"
                          onClick={handleClick}
                      >
                        <div className={`hamburger hamburger--slider  ${ isOpen ? 'is-active' : ''}`}>
                          <div className="hamburger-box">
                            <div className="hamburger-inner" />
                          </div>
                        </div>
                      </div>
                </div>
              </div>
            </div>
          </div>
          <div className="header__menu sura-header">
            <div className="container">
              <nav className="main-menu main-menu_theme_3">
                <ul className="main-menu__list">
                {
                    list.length > 0 &&
                    list.map( (item, index) => {
                        return (
                            <li key={index} className="main-menu__item">
                                <NavLink className="main-menu__link" to={`/${item.slug}`} >
                                    {item.title}
                                </NavLink>
                                    {
                                        item['list-service-menu'] && item['list-service-menu'].length > 0 &&
                                        <ul>
                                            {
                                                item['list-service-menu'].map( (innerItem) => {
                                                    return(
                                                        <li key={innerItem.id} className='nav-item'>
                                                            <NavLink exact to={`/${item.slug}/${innerItem.slug}`} className="main-menu__link">
                                                                {innerItem.title}
                                                            </NavLink>
                                                        </li>
                                                    )
                                                })
                                            }
                                        </ul>
                                    }
                                    {
                                        item['list-brands-menu'] && item['list-brands-menu'].length > 0 &&
                                        <ul className='second-list'>
                                            {
                                                item['list-brands-menu'].map( (innerItem) => {
                                                    return(
                                                        <li key={innerItem.id} className="main-menu__item">
                                                            <NavLink exact to={`/${item.slug}/${innerItem.slug}`} className="main-menu__link" >
                                                                {innerItem.title}
                                                            </NavLink>
                                                        </li>
                                                    )
                                                })
                                            }
                                        </ul>
                                    }

                            </li>
                        )
                    })
                }

                </ul>
              </nav>
            </div>
          </div>
          <div className={`header__menu-mobile ${isOpen ? 'show' : ''}`}>
            <div className="mobile-menu mobile-menu_theme_3">
              <div className="container mobile-menu__container">
                <nav className="main-menu main-menu_theme_3 mobile-menu__menu">
                  <ul className="main-menu__list">
                      {
                          list.length > 0 &&
                          list.map( (item, index) => {
                              return (
                                  <li key={index} className="main-menu__item">
                                      <NavLink className="main-menu__link" to={`/${item.slug}`} onClick={handleClick}>
                                          {item.title}
                                      </NavLink>
                                  </li>
                              )
                          })
                      }

                  </ul>
                </nav>
                <div className="mobile-menu__time"><svg className="icon icon_clock" role="img">
                    <use xlinkHref="assets/images/sprite.svg#clock" />
                  </svg><span>Пн-Пт с 10.00 до 18.00, Сб-Вс выходной</span></div>
                <div className="mobile-menu__phone">
                    <a className="phone phone_theme_3" href="tel:+78007008000">
                    {/* Если указана иконка*/}<svg className="icon icon_phone-call phone__icon" role="img">
                        <use href={sprite + "#phone-call"} />
                    </svg>
                    {/*Если потребуются свои элементы, то можно указать поcле +phone()*/}<span className="phone__number">8 800 700 8000</span></a><a className="phone phone_theme_3" href="tel:+78007008000">
                    {/* Если указана иконка*/}<svg className="icon icon_phone-call phone__icon" role="img">
                        <use href={sprite + "#phone-call"} />
                    </svg>
                    {/*Если потребуются свои элементы, то можно указать поcле +phone()*/}<span className="phone__number">8 800 700 8000</span></a></div>
                <div className="mobile-menu__btn">
                  <button
                      className="button button_theme_12"
                      type="button"
                      onClick={props.handleShowModal}
                  >
                    <svg className="icon icon_phone-call button__icon" role="img">
                        <use href={sprite + "#phone-call"} />
                    </svg>
                    <span className="button__text">Заказать звонок</span>
                  </button>
                </div>
              </div>
            </div>
          </div>
        </header>
    )

     
}

export default Header
